import React from "react";
import {Area, AreaChart, ResponsiveContainer} from "recharts";
import ChartCard from "./ChartCard";
import UserImages from "./UserImages";
import RecentActivity from "../Author/RecentActivity";
import CurrentPlan from "./CurrentPlan";
import DealsClosedCard from "./DealsClosedCard";
import PropertiesCard from "./PropertiesCard";
import Widget from "components/Widget";
import ContainerHeader from "components/ContainerHeader";
import IntlMessages from "util/IntlMessages";
import UserProfileCard from 'components/dashboard/Common/userProfileCard/UserProfileCard';
import HorizontalNonLinearAlternativeLabelStepper from 'app/routes/components/routes/stepper/nonLinear/HorizontalNonLinearAlternativeLabelStepper';
import {increamentData} from "./mdata";
import {recentActivity} from "../Author/data";
import CardBox from 'components/CardBox';
import {useDropzone} from 'react-dropzone';
import AddPublicationForm from 'app/routes/components/routes/stepper/linear/AddPublicationForm';
import BookProfileCard from 'components/dashboard/Common/bookProfileCard/BookProfileCard';


const Publication = ({match}) => {
  return (

    <div className="dashboard animated slideInUpTiny animation-duration-3">

      <ContainerHeader match={match} title={<IntlMessages id="sidebar.dashboard.publication"/>}/>
      <div className="row">
        <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
          <ChartCard chartProperties={{
            title: 'Fiction',
            prize: '26,873',
            icon: 'stats',
            bgColor: 'indigo',
            styleName: 'up',
            desc: 'This week',
            percent: '3',
          }}
                     children={<ResponsiveContainer width="100%" height={75}>
                       <AreaChart data={increamentData}
                                  margin={{top: 0, right: 0, left: 0, bottom: 0}}>
                         <Area dataKey='pv' strokeWidth={0} stackId="2" stroke='#273894' fill="#273894"
                               fillOpacity={1}/>
                       </AreaChart>
                     </ResponsiveContainer>}
          />

        </div>
        <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
          <ChartCard
            chartProperties={{
              title: 'Politics',
              prize: '384',
              icon: 'stats',
              bgColor: 'pink accent-2',
              styleName: 'up',
              desc: '7 New cities this week',
              percent: '',
            }}
            children={<ResponsiveContainer width="100%" height={75}>
              <AreaChart data={increamentData}
                         margin={{top: 0, right: 0, left: 0, bottom: 0}}>
                <Area dataKey='pv' type='monotone' strokeWidth={0} stackId="2" stroke='#da2361'
                      fill='#da2361'
                      fillOpacity={1}/>
              </AreaChart>
            </ResponsiveContainer>}
          />
        </div>
        <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
          <ChartCard
            chartProperties={{
              title: 'Drama',
              prize: '284,729',
              icon: 'stats',
              bgColor: 'info',
              styleName: 'down',
              desc: 'Avg. 327 visits daily',
              percent: '',
            }}
            children={<ResponsiveContainer width="100%" height={75}>
              <AreaChart data={increamentData}
                         margin={{top: 0, right: 0, left: 0, bottom: 0}}>
                <Area dataKey='pv' strokeWidth={0} stackId="2" stroke='#0c8e9f' fill='#0c8e9f'
                      fillOpacity={1}/>
              </AreaChart>
            </ResponsiveContainer>}
          />
        </div>
        <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
          <ChartCard
            chartProperties={{
              title: 'Science',
              prize: '87,239',
              icon: 'stats',
              bgColor: 'success',
              styleName: 'down',
              desc: 'from past month',
              percent: '39%',
            }}
            children={<ResponsiveContainer width="100%" height={75}>
              <AreaChart data={increamentData}
                         margin={{top: 0, right: 0, left: 0, bottom: 0}}>
                <Area dataKey='pv' strokeWidth={0} stackId="2" stroke='#3a983e' fill='#3a983e'
                      fillOpacity={1}/>
              </AreaChart>
            </ResponsiveContainer>}
          />
        </div>

        {/* <div className="col-xl-4 col-lg-4 col-md-12 col-12 order-sm-2">
          <Widget>
            <RecentActivity shape="rounded" recentList={recentActivity}/>
          </Widget>
        </div> */}

        {/* Publications */}
        <div className="col-lg-3 col-sm-6 col-12">
            <BookProfileCard styleName="pb-4" headerStyle="bg-gradient-primary"/>
          </div>

          <div className="col-lg-3 col-sm-6 col-12">
            <BookProfileCard styleName="pb-4" headerStyle="bg-gradient-primary"/>
          </div>

          <div className="col-lg-3 col-sm-6 col-12">
            <BookProfileCard styleName="pb-4" headerStyle="bg-gradient-primary"/>
          </div>

          <div className="col-lg-3 col-sm-6 col-12">
            <BookProfileCard styleName="pb-4" headerStyle="bg-gradient-primary"/>
          </div>
      </div>

           
      <div className="animated slideInUpTiny animation-duration-3">
      <div className="row mb-md-3">
        <CardBox styleName="col-lg-12" childrenStyle="d-flex justify-content-center"
                 heading={<IntlMessages
                   id="component.stepper.horizontalLinearAlternativeLabel"/>}
                 headerOutside>
          <AddPublicationForm/>
        </CardBox>
      </div>
      </div>
    </div>
  );
};

export default Publication;
